import { connect } from 'react-redux';
import FormUserChangeForgottenPassword from '../../../Components/UIElement/Forms/FormUserChangeForgottenPassword';
import { changePassword } from '../../../Redux/actions/user.actions';
import { getParamsFromUrl } from '../../../Redux/actions/form.actions';

const mapState = null;

const mapDispatch = (dispatch) => ({
  changePassword: () => {
    dispatch(changePassword());
  },
  getParamsFromUrl: (payload) => {
    dispatch(getParamsFromUrl(payload));
  },
});

export default connect(mapState, mapDispatch)(FormUserChangeForgottenPassword);
