import React, { useEffect } from 'react';

import Field from '../../../Containers/UIElement/Forms/Field/FieldContainer';
import FormContainer from '../../../Containers/UIElement/Forms/FormContainer/FormContainerContainer';
import ButtonForm from './ButtonForm/ButtonForm';
import { useParams } from 'react-router-dom';

const FormUserChangePassword = ({ changePassword, getParamsFromUrl }) => {
  const { id, token } = useParams();
  useEffect(() => {
    getParamsFromUrl({ id, token });
  }, [getParamsFromUrl, id, token]);
  return (
    <FormContainer onSubmitEvent={changePassword}>
      <Field
        label="Mot de passe"
        name="password"
        type="password"
        placeholder="Mot de passe"
        pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
        title="Le mot de passe doit être au moins de 8 caractères, contenir une majuscule et un nombre"
        required
      />
      <Field
        label="Confirmer"
        name="passwordConfirm"
        type="password"
        placeholder="Répéter mot de passe"
        pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
        title="Le mot de passe doit être identique"
        required
      />
      <ButtonForm type="submit">modifier</ButtonForm>
    </FormContainer>
  );
};

export default FormUserChangePassword;
